'use client';
import { costemStorage } from "@/utils";
import axios, { AxiosError } from "axios";
import { useState } from "react"

export const useAuth = () => {
    const [auth, setAuth] = useState<any>(costemStorage.local.get('auth', false));
    const [loding, setLoding] = useState(false);

    const authPath = async (pw: string) => {
        setLoding(true);
        try {
            const response = await axios.post(
                '/api',
                { password: pw, },
                { headers: { 'Content-Type': 'application/json', } }
            );
            if (response.status === 200) {
                setAuth(costemStorage.local.set('auth', true));
            }
        }
        catch (error) {
            const axiosError = error as AxiosError;
            const errorMessage = (axiosError?.response?.data as { message?: string })?.message;
            if (errorMessage) { alert(errorMessage); } 
            else { alert('Unexpected Error Occurred During API Execution'); }
        }
        finally { setLoding(false); }
    };

    const initAuth = () => {
        costemStorage.local.remove('authPw');
        setAuth(costemStorage.local.get('authPw', false));
        setLoding(false);
    }

    return {
        isAuth: auth === 'true',
        login: authPath,
        loding: loding,
        initAuth,
    };
}